import {WebAPI} from './web-api';
import {inject, NewInstance} from 'aurelia-framework';
import {LogManager} from 'aurelia-framework';
import { ValidationController, ValidationRules } from 'aurelia-validation';
import { MaterializeFormValidationRenderer } from 'aurelia-materialize-bridge';
import { MdToastService } from 'aurelia-materialize-bridge';
import {Router} from 'aurelia-router';
import moment from 'moment';

let log = LogManager.getLogger('contact-edit');

@inject(WebAPI, NewInstance.of(ValidationController), MdToastService, Router)
export class ContactNew {
  rules = ValidationRules
    .ensure('contactName')
      .required()
        .withMessage('連絡先名称は必須です')
    .ensure('orderDisplay')
      .required()
        .withMessage('表示順は必須です')
      .matches(/[0-9]+/)
        .withMessage('数値を指定して下さい')
      .rules;

  constructor(api, controller, toast, router) {
    this.api = api;
    this.controller = controller;
    this.controller.addRenderer(new MaterializeFormValidationRenderer());
    this.toast = toast;
    this.router = router;

    this.advancedOptions = {
      closeOnSelect: true,
      closeOnClear: true,
      monthsFull: [ '１月', '２月', '３月', '４月', '５月', '６月', '７月', '８月', '９月', '１０月', '１１月', '１２月' ],
      monthsShort: [ '1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月' ],
      weekdaysFull: [ '日曜日', '月曜日', '火曜日', '水曜日', '木曜日', '金曜日', '土曜日' ],
      weekdaysShort: [ '日曜', '月曜', '火曜', '水曜', '木曜', '金曜', '土曜' ],
      weekdaysLetter: [ '日', '月', '火', '水', '木', '金', '土' ],
      today: '今日',
      clear: '消去',
      close: '閉じる',
      selectYears: 5,
      editable: false,
      showIcon: false,
      format: 'yyyy/mm/dd',
      formatSubmit: 'yyyy/mm/dd'
    };

    this.contactName = '';
    this.orderDisplay = '';
    this.note = '';
    this.flgChangeDate = null;
    this.updateEmplyeeCD = null;
    this.updateDatetime = null;
    this.dispLoading = false;
    log.info('ContactNew-constructor');
  }

  activate(params) {
    log.info(params);
    this.dispLoading = true;
    this.api.getContact(params.id).then(results => {
      log.info('Success');
      log.info(results);
      if (results.length > 0) {
        this.contactCd = results[0].MCNTCT_ContactCD;
        this.contactName = results[0].MCNTCT_ContactName;
        this.orderDisplay = results[0].MCNTCT_OrderDisplay;
        this.note = results[0].MCNTCT_Note;
        this.activeFlg = results[0].MCNTCT_ActiveFlg === 1 ? true : false;
        this.updateEmplyeeCD = results[0].MCNTCT_UpdateEmployeeCD;
        this.updateDatetime = moment(results[0].MCNTCT_UpdateDatetime).format('YYYY/MM/DD HH:mm:ss');
        if (results[0].MCNTCT_FlgChangeDate) {
          this.flgChangeDate = moment(results[0].MCNTCT_FlgChangeDate).format('YYYY/MM/DD');
        }
      }
      this.dispLoading = false;
    }).catch(error => {
      log.info('Error!');
      log.info(error);
      this.dispLoading = false;
    });
  }

  validateModel() {
    this.controller.validate().then(v => {
      if (v.valid) {
        log.info('pass-1');
        this.dispLoading = true;
        let body = [{
          'contactCD': this.contactCd,
          'contactName': this.contactName,
          'orderDisplay': this.orderDisplay,
          'activeFlg': this.activeFlg ? 1 : 0,
          'flgChangeDate': this.flgChangeDate,
          'note': this.note,
          'updateEmployeeCD': 'izu_t'
        }];
        this.api.updateContact(body).then(results => {
          log.info('Success');
          log.info(results);
          this.dispLoading = false;
          this.router.navigate('/');
        }).catch(error => {
          log.info('Error!');
          log.info(error);
          this.dispLoading = false;
        });
      } else {
        //this.message = 'You have errors!';
        this.toast.show('You have errors!', 4000, 'red white-text');
      }
    });
  }

  created() {
    log.info('ContactEdit-created');
  }

  clearFlgChangeDate() {
    if (this.activeFlg) {
      this.flgChangeDate = null;
    } else {
      this.flgChangeDate = moment().format('YYYY/MM/DD');
    }
    return true;
  }
}
